@import "./fonts.css";

* {
  -webkit-font-smoothing: antialiased;
	box-sizing: border-box;
}

body {
  margin: 0;
  box-sizing: border-box;
  font-family: "Agrandir", "Maison Neue", "Source Sans Pro", sans-serif;
  color: #323024;
  background: #fff;
}


#hero h1 {
  font-family: "Agrandir";
  font-weight: 500;
  font-size: 2.62em;
  letter-spacing: -1px;
  width: calc(100% - 40px);
  margin: 50px auto 0 auto;
  max-width: 700px;
  line-height: 1.2;
  margin: 0;
  z-index: 1;
}

#hero h2 {
  font-family: "Maison Neue";
  font-weight: 300;
  color: #279fc1;
  letter-spacing: -1px;
}

#hero h2 + h2 {
  margin-top: 50px;
  cursor: pointer;
}

#hero .copy-switcher {
  display: inline-block;
  vertical-align: top;
  overflow: hidden;
  height: 30px;
}

#hero .copy--wrapper {
  display: inline-block;
  vertical-align: top;
}

#hero .copy--wrapper.copy--up {
  transform: translateY(-30px);
  transition: all 0.3s ease;
}

#hero .copy-switcher .copy {
  display: block;
  margin-bottom: 6px;
  font-weight: 500;
}

.arrow {
  display: inline-block;
  font-family: "Times New Roman";
  transform: scaleX(0.8);
  font-weight: 300;
  animation-name: ArrowPulse;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  font-size: 1.2em;
}

@keyframes ArrowPulse {
  0% { transform: scaleX(0.8) translateX(0px); opacity: 0.5 }
  50% { transform: scaleX(0.8) translateX(10px); opacity: 1 }
  100% { transform: scaleX(0.8) translateX(0px); opacity: 0.5 }
}

.image-container {
  position: absolute;
  left: 0%;
  top: 0;
  width: 100%;
  max-width: 950px;
}

.image-container img {
  position: absolute;
  width: 100%;
  transform: translateY(-100px);
  filter: brightness(1.1);
  z-index: 1;
}

#hero {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  transition: opacity 0.3s linear;
}

#hero .left {
  position: relative;
  max-width: 800px;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

.people {
  position: absolute;
  width: 60vw;
  height: 100vh;
  right: 0;
  top: 0;
  background-image: url(../assets/people.jpg);
  background-size: auto 110vh;
}

#links {
  position: absolute;
  z-index: 3;
  right: 50px;
  top: 50px;
  padding: 20px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0px 0px 30px -5px #fff, 0px 0px 50px -20px #fff, 0px 0px 50px -20px #fff;
  color: #fffbef;
}

#links .links--bg-blur {
  position: absolute;
  width: 100%;
  height: 100%;
  filter: blur(10px) brightness(0.75) saturate(1.5);
  transform: scale(2);
  top: 0;
  z-index: -1;

  background-image: url(../assets/people.jpg);
  background-size: auto 55vh;
  background-position: 30vh auto;
}

#links a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
  font-family: "Maison Neue", sans-serif;
  padding: 5px 10px 1px 10px;
  font-weight: 500;
  transition: all 0.1s ease;
}

#links a:hover {
  color: #fff;
  transform: scale(1.2);
}

#logo {
  width: 70px;
  position: absolute;
  z-index: 2;
  left: 50px;
  top: 50px;
}

#app {
  position: absolute;
  margin-top: 100vh;
  z-index: 1;

  min-height: 100vh;
  width: 100vw;

  overflow: hidden;

  background: #fff;
  box-shadow: 0px 0px 100px -20px #32302420;
}

#app .art--save-more {
  display: inline-block;
  vertical-align: top;
  transform: translate(120px, 0);
  margin-left: -120px;
}

#app .art--save-more--rotate {
  transform: rotate(20deg);
}

#app .art--save-more h1 {
  font-size: 4.5em;
  margin: 0;
  line-height: 0.8;
  font-weight: 500;
  font-family: "Maison Neue", "Helvetica Neue", Helvetica, sans-serif;

  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #ebc975;
  color: transparent;

  letter-spacing: -0.07em;
}

#app .art--save-more h1.bold {
  color: #e090a7;
  -webkit-text-stroke-color: transparent;
}

#app .section {
  width: 100%;
  margin: 200px 0px;
}

#app .section {
  text-align: center;
  width: 100vw;
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
}

.wrapper--switch-to-save {
  background: #ebc97520;
}

#app .section--things-to-know .art--three-things-to-know h1 {
  font-size: 2.5em;
  margin: 0 0 50px 0;
}

.feature {
  position: relative;
	width: calc(33% - 40px);
  height: 400px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  vertical-align: top;
  margin: 0px 20px;
}

.feature.feature--company-sizes img {
  opacity: 0.08;
}

.feature.feature--save-time img {
  max-width: 300px;
  opacity: 0.08;
}

#app .feature h1 {
	font-size: 1.8vw;
}

.feature h1 .highlight {
  background: none;
  border-radius: 0px;
  padding-bottom: 1px;
  border-bottom: 4px solid #e090a7;
}

.feature img {
  position: absolute;
  width: 90%;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.3;
}

#app .section--switch-to-save {
  padding: 100px 0px 150px 0px;
  margin: 0px auto;
}

#app .section.section--switch-to-save .section--text h3 {
	font-size: 1.8em;
	height: 0;
	margin-top: 0;
}

#app .section--switch-to-save h3.header--ghost {
	display: none;
}

#app .section.section--switch-to-save .section--text h3:not(.header--ghost) {
	height: auto;
}

#app .section.section--switch-to-save .section--text h3:not(.header--ghost) {
	text-shadow: 0px 0px 10px #fdf8ee, 0px 0px 10px #fdf8ee, 0px 0px 10px #fdf8ee,0px 0px 10px #fdf8ee,0px 0px 10px #fdf8ee;
}

#app .section.section--switch-to-save .section--text h3.header--ghost {
	-webkit-text-stroke-width: 1px;
	color: transparent;
	z-index: -1;
	position: relative;
}

#app .section.section--switch-to-save .section--text h3.header--ghost.header--2 {
	transform: translateY(-10px);
	-webkit-text-stroke-color: #55e0aa;
	opacity: 0.5;
}

#app .section.section--switch-to-save .section--text h3.header--ghost.header--3 {
	transform: translateY(-18px);
	-webkit-text-stroke-color: #e0a555;
	opacity: 0.4;
}

#app .section.section--switch-to-save .section--text h3.header--ghost.header--4 {
	transform: translateY(-26px);
	-webkit-text-stroke-color: #e090a7;
	opacity: 0.15;
}

#app .section.section--switch-to-save .section--text h3.header--4 {
	margin-bottom: 50px;
}

#app .switch-to-save--calculator {
  display: inline-block;
  vertical-align: top;
  width: calc(100% - 490px);
  margin-right: 50px;
  margin-left: 100px;
  height: 510px;
  transform: translateX(100px);
}

#app .section .section--text {
  display: inline-flex;
  vertical-align: top;
  justify-content: center;
  align-items: center;
  flex-direction: column;
	z-index: 1;
	position: relative;
}

#app .section .section--text p {
  font-family: "Maison Neue";
  text-align: left;
  line-height: 1.5;
  opacity: 0.7;
  margin: 5px 0;
}

#app h1 {
  font-weight: 500;
  font-size: 2em;
  line-height: 1.4;
}

.maison {
  font-family: "Maison Neue";
  letter-spacing: -2px;
}

#app .section--text h1 {
  max-width: 800px;
  text-align: left;
}

#app .section--text h2 {
  color: #32302480;
}


#app .section--text h3 {
  text-align: left;
  font-weight: 400;
  font-size: 2em;
  margin-bottom: 0;
}

#app h2 {
  font-family: "Maison Neue";
  font-size: 1.3em;
  font-weight: 400;
  line-height: 1.5;
  max-width: 800px;
  width: 100%;
  text-align: left;
  display: inline-block;
  margin-top: 0;
}

#app .section--logos .section--text {
  width: calc(100% - 750px);
  margin: 0px 50px 0px 0px;
  height: 500px;
}

#app .switch-to-save-triangle {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 200px 0 0 100vw;
  border-color: transparent transparent transparent #ebc97520;
  margin-top: -100px;
}

#app .switch-to-save--calculator h2 {
  font-size: 1em;
}

#app .switch-to-save--calculator h1 {
  font-weight: 500;
}

.section .logos {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 500px;
	height: 700px;
	overflow: hidden;
	margin: -100px 0px -100px 50px;
	transition: all 0.2s linear;
}

.section .logos--inner-wrapper {
	position: relative;
	transition: all 0.2s linear;
}

.section .logos::before,
.section .logos::after {
	content: " ";
	position: absolute;
	left: 0;
	width: 100%;
	height: 200px;
	z-index: 1;
}

.section .logos::before {
	top: 0;
	background: linear-gradient(0deg, #ffffff00, #fff);
}

.section .logos::after {
	bottom: 0;
	background: linear-gradient(180deg, #ffffff00, #fff);
}

.section .logos img {
	position: relative;
	display: block;
	left: 0;
	margin: 60px 0px;
}

.section .logos .logo--figma {
	left: 12.5%;
	height: 65px;
}

.section .logos .logo--circleci {
	height: 35px;
	left: 50%;
}

.section .logos .logo--cloudflare {
	height: 60px;
	left: 7.5%;
}

.section .logos .logo--iterable {
	height: 40px;
	left: 65%;
}

.section .logos .logo--pilot {
	height: 45px;
	left: 5%;
}

.section .logos .logo--popsugar {
	height: 22px;
	left: 37.5%;
	margin-bottom: 70px;
}

.section .logos .logo--scale {
	left: 20%;
	height: 35px;
}

#app .section--dashboard {
	position: relative;
	z-index: 2;
}

#app .section--dashboard .section--text {
  width: calc(100% - 900px);
  height: 550px;
}

#app .section--dashboard .section--text h2.bullet::before {
	content: "•";
	position: absolute;
	transform: translateX(-20px) translateY(-7px);
	font-size: 1.5em;
}

#app .section--dashboard .section--text h2.bullet {
	padding-left: 20px;
	margin-left: 10px;
}

#app .section--dashboard img {
  width: 700px;
  z-index: 2;
  position: relative;
  transform: translateY(70px);
}

#app .section--dashboard .image-wrapper {
  display: inline-block;
  position: relative;
	margin-left: 50px;
}

#app .section .responsive {
	display: none;
}

#app .section--dashboard .image-wrapper .bg {
  position: absolute;
  width: 130%;
  height: 150%;
  top: -30%;
  left: -17%;
	z-index: 0;
}

#app .section--dashboard .image-wrapper .image--form {
  width: 600px;
  position: absolute;
  top: -100px;
  left: 50px;
  z-index: 1;
  opacity: 0.7;
  filter: blur(1px);
}

#app .timeline--container {
  position: relative;
  font-family: "Maison Neue", "Helvetica Neue", Helvetica, sans-serif;
  width: 1800px;
  margin: 0px 0px 100px 0px;
  padding: 0px 100px;
  height: 300px;
  transition: transform 0.3s linear;
}

#app .timeline--container .timeline--legend {
  transform: translateY(190px);
}

#app .tick {
  position: absolute;
  transform: rotate(-90deg);
}

#app .tick.tick--week {
  width: 45px;
  text-align: right;
  opacity: 0.5;
  transform: translateX(-23px) rotate(-90deg);
}

#app .tick.tick--month {
  width: 100px;
  text-align: right;
  transform: rotate(-90deg) translateX(-70px) translateY(-48px);
  font-weight: 500;
}

#app .line {
  position: absolute;
  width: 1px;
  background: #323024;
  transform: translateY(-55px);
}

#app .line.line--month {
  height: 30px;
}

#app .line.line--week {
  height: 10px;
  transform: translateY(-35px);
  opacity: 0.4;
}

#app .line.line--day {
  height: 5px;
  transform: translateY(-30px);
  opacity: 0.1;
}

#app .tab-switcher {
  font-family: "Maison Neue", "Helvetica Neue", Helvetica, sans-serif;
  margin-bottom: 50px;
  margin-top: 100px;
  cursor: pointer;
}

#app .tab-switcher .tab {
  padding: 10px 15px 8px 15px;
  margin: 0 5px;
  border-radius: 10px;
  display: inline-block;
  transition: all 0.3s ease;
}

#app .tab-switcher .tab:hover:not(.active) {
  background: #6b390010;
}

#app .tab-switcher .tab.active {
  background: #6b390020;
}

#app .timeline--events .event--employers,
#app .timeline--events .event--sparrows {
  transform: translateY(-120px);
}

#app .timeline--events > div {
  transition: all 0.3s ease;
}

#app .timeline--events > .hide {
  opacity: 0;
  pointer-events: none;
}

#app .timeline--events .event--employees {
  transform: translateY(-50px);
}

#app .timeline--events .event {
  position: absolute;
  white-space: nowrap;
  font-size: 0.8em;
  font-weight: 400;
}

#app .event.event--sparrow .event--event-bar {
  background: #8cd2e6;
}

#app .event.event--employer .event--event-bar {
  background: #c7b5b9;
}

#app .event.event--employee .event--event-bar {
  background: #ebc975;
}

#app .event--event-bar {
  padding: 3px;
  border-radius: 3px;
}

#app .timeline--example {
  font-family: "Maison Neue", "Helvetica Neue", Helvetica, sans-serif;
  text-align: left;
  margin-bottom: 20px;
  margin-left: 100px;
  color: #32302480;
}

#app .timeline--example .bold {
  color: #323024;
}

#app .wrapper--footer {
  background: #333538;
  padding: 0.1px 0px;
  color: #fff1d4;
}


#app .wrapper--footer .section--footer {
  margin: 120px auto 100px auto;
}

#app .wrapper--footer h1 {
	font-family: "Maison Neue", "Helvetica Neue", Helvetica, sans-serif;
  max-width: 580px;
  margin: 0 auto 20px auto;
  text-align: left;
}

span.input {
  border-bottom: 4px solid #e090a7;
  outline: none;
  line-height: 0.8;
  display: inline-block;
}

span.input + svg {
  height: 0.8em;
  opacity: 0.5;
  color: #323024;
  margin-left: 5px;
  transform: translateY(1px);
  transition: all 0.3s ease;
  cursor: pointer;
}

span.input:hover + svg,
span.input + svg:hover {
  opacity: 1;
}

span.input::before {
  content: attr(data-prefix);
}

span.input::after {
  content: attr(data-suffix);
}

span.input:empty::before {
  content: attr(data-placeholder);
  opacity: 0.5;
}

span.input:empty::after {
  content: "";
}

h1 span.highlight {
  background: #ffde8c80;
  padding: 10px 4px 6px 4px;
  margin-left: -6px;
  border-radius: 10px;
}

.bold {
  font-weight: 500;
}

form {
  display: block;
  margin: 0 auto;
	width: 100%;
	max-width: 600px;
  font-family: "Maison Neue";
}

label {
  margin: 10px;
  width: calc(100% - 20px);
  display: inline-block;
  vertical-align: top;
  text-align: left;
  background: #00000040;
  color: #fff1d480;
  padding: 15px;
  border-radius: 8px;
  font-size: 0.8em;
  font-weight: 500;
	transition: all 0.3s ease;
}

form label:hover,
form label:focus-within {
	color: #fff1d4a0;
}

label.half-size {
  width: calc(50% - 20px);
}

label input {
  font-family: "Maison Neue";
  font-size: 1.5em;
  width: 100%;
  background: transparent;
  border: none;
  outline: none;
  padding: 10px 0px 0px 0px;
  color: #fff1d4;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  -webkit-text-fill-color: #fff1d4;
  -webkit-box-shadow: 0 0 0px 1000px #26282a inset;
  transition: background-color 5000s ease-in-out 0s;
}

#app form h2 {
  text-align: right;
  transform: translateX(-15px);
  margin-top: 20px;
	cursor: pointer;
}

#app form h2 button {
  font-size: inherit;
  background: transparent;
  color: inherit;
  border: none;
  outline: none;
  cursor: pointer;
}

::placeholder {
  color: #fff1d440;
}

@media (max-width: 1500px) {
	#app .section {
		margin-left: 50px;
		margin-right: 50px;
		width: calc(100% - 100px);
	}

	#app .section--things-to-know .art--three-things-to-know h1 {
		max-width: 800px;
		margin-left: auto;
		margin-right: auto;
	}
}

@media (max-width: 1350px) {
	#app .section--dashboard img {
		max-width: 600px;
	}

	#app .section--dashboard .image-wrapper .image--form {
		max-width: 500px;
	}

	#app .section--dashboard .image-wrapper .bg {
		max-width: none;
	}

	#app .section--dashboard .section--text,
	#app .section--logos .section--text {
	  width: calc(100% - 600px);
	}

	#app .section--dashboard .section--text {
		width: calc(100% - 650px);
		height: 470px;
	}
}

@media (max-width: 1280px) {
	#app .section--text h1 {
		font-size: 1.7em;
	}

	#app h2 {
		font-size: 1.2em;
	}

  #app .switch-to-save--calculator {
    width: calc(100% - 540px);
    margin-left: 150px;
  }
}

@media (max-width: 1200px) {
  #app .switch-to-save--calculator h2 {
    font-size: 1em;
  }

	#app .section .logos {
		width: 400px;
	}

	#app .section--dashboard img {
		max-width: 500px;
	}

	#app .section--dashboard .image-wrapper .image--form {
		max-width: 400px;
	}

	#app .section--dashboard .section--text {
		width: calc(100% - 550px);
		height: 400px;
	}

	.section .logos .logo--iterable {
		left: 50%;
	}

	#app .section--logos .section--text {
		width: calc(100% - 500px);
	}

	#app .section--things-to-know .art--three-things-to-know h1 {
		font-size: 2em;
	}
}

@media (min-width: 1500px) {
  #app .timeline--container {
    width: 1400px;
    margin-right: 0;
  }
}

@media (max-width: 1050px) {
  #app .art--save-more {
    transform: translate(30px, -30px);
    margin-left: -120px;
  }

	#app .section.section--switch-to-save {
		padding-top: 50px;
	}

	#app .section.section--switch-to-save .section--text {
		width: 100%;
		transform: none;
	}

  #app .art--save-more {
    display: none;
  }

  #app .switch-to-save--calculator {
    width: calc(100% - 400px);
    margin-left: 100px;
  }

	#app .section.section--switch-to-save .section--text h3 {
		font-weight: 500;
	}

	#app .section.section--switch-to-save .section--text h3:not(.header--ghost) {
		height: 0;
		color: #7097f1;
	}

	#app .section--switch-to-save h3.header--ghost {
		display: block;
	}

	#app .section .section--text {
		width: 100%;
		max-width: 600px;
		margin-left: 0;
		margin-right: 0;
		height: auto;
	}

	#app .section .responsive {
		display: inline-block;
	}

	#app .section--dashboard .image-wrapper.responsive {
		margin: 50px 0px;
	}

	#app .logos.responsive {
		margin-top: 0px;
		margin-left: 0px;
	}

	#app .section--dashboard img {
		max-width: 100%;
	}

	#app .section--dashboard .image-wrapper .image--form {
		width: auto;
		max-width: calc(100% - 100px);
	}

	#app .section .logos:not(.responsive),
	#app .section--dashboard .image-wrapper:not(.responsive) {
		display: none;
	}

	#app .section--dashboard .image-wrapper .bg {
    width: 100%;
    top: -50%;
    left: 0;
    opacity: 0.5;
	}

	#app .section.section--things-to-know {
		margin-bottom: 100px;
	}

	#app .section.section--dashboard,
	#app .section.section--logos {
		margin: 50px;
	}

	#app .section.section--logos {
		margin-top: 50px;
	}

	#app .feature {
		margin: 0;
		width: calc(33%);
		height: 300px;
	}

	#app .section--logos .logos {
		margin-bottom: 0px;
	}

	#app .section--logos .section--text {
		margin-bottom: 100px;
	}

	#app .section--text {
		margin-top: 50px;
	}
}

@media (max-width: 850px) {
	.section--things-to-know > div:nth-of-type(3) {
		display: none;
	}

	#app .feature {
		width: calc(50%);
	}

	#app .feature h1 {
